import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import editMaster from '@/components/editMaster'
import NewMaster from '@/components/NewMaster'
import viewMaster from '@/components/viewMaster'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-master',
      component: NewMaster
    },
    {
      path: '/edit/:mobile',
      name: 'edit-master',
      component: editMaster
    },
    {
      path: '/:mobile',
      name: 'view-master',
      component: viewMaster
    },
  ]

})



